@keyframes fadeCycle {
    0%, 35% { opacity: 1; }
    45%, 90% { opacity: 0; }
    100% { opacity: 1; }
}

@keyframes fadeCycleText {
    0%, 45% { opacity: 0; }
    55%, 80% { opacity: 1; }
    90%, 100% { opacity: 0; }
}

@keyframes bounce {
    0%, 100% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.2);
    }
}
@keyframes scaleBounce {
    0%, 100% {
        transform: translate(-50%, -50%) scale(1);
    }
    50% {
        transform: translate(-50%, -50%) scale(1.2);
    }
}
