@keyframes bounceSelect {
    0% { 
        transform: rotate(0deg) scale(1); 
        opacity: 1;
        color: white;
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 1), 0 0 10px rgba(0, 0, 0, 0.8);
    }
    10% { 
        color: transparent; 
        text-shadow: none; 
    }
    20% { transform: rotate(180deg) scale(1.1); } 
    45% { transform: rotate(360deg) scale(1.05); } 
    70% { transform: rotate(540deg) scale(1.0); } 
    100% { 
        transform: rotate(900deg) scale(0.25); 
        opacity: 0; 
        color: transparent;
        text-shadow: none;
    }
}

.court-confirm-btn.end-game-ball {
    background-image: url('source/tennis-ball-red.png');
    background-color: transparent;
    background-size: cover;
    background-position: center;
    border: none;
    width: 120px;
    height: 120px;
    border-radius: 50%;
    color: white; 
    font-weight: bold;
    font-size: calc(1.2rem * var(--font-size-multiplier));
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 1), 0 0 10px rgba(0, 0, 0, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 0 0 120px !important;
    padding: 0 !important;
    max-height: none !important;
    height: 120px !important;
    transition: transform 0.2s;
    -webkit-tap-highlight-color: transparent;
}

.court-confirm-btn.end-game-ball:hover {
    transform: scale(1.05);
}

.court-confirm-btn.end-game-ball.hide-anim {
    animation: bounceSelect 2s forwards;

}

@keyframes ballHop {
    0% {
        transform: scale(1.2);
        opacity: 1;
        color: white;
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 1), 0 0 10px rgba(0, 0, 0, 0.8);
    }
    5% {
        color: transparent;
        text-shadow: none;
    }
    15% {
        transform: scale(0.7);
    }
    30% {
        transform: scale(1);
    }
    45% {
        transform: scale(0.75);
    }
    60% {
        transform: scale(0.85);
    }
    75% {
        transform: scale(0.78);
    }
    90% {
        transform: scale(0.82);
    }
    100% {
        transform: scale(0);
        opacity: 0;
        color: transparent;
        text-shadow: none;
    }
}

.court-confirm-btn.select-court.start-game-hop {
    animation: ballHop 2s forwards;

}

@keyframes hitWinner {
    0% {
        transform: translate(0, 0) rotate(0) scale(1);
        opacity: 1;
        color: white;
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 1), 0 0 10px rgba(0, 0, 0, 0.8);
    }
    10% {
        color: transparent;
        text-shadow: none;
    }
    100% {
        transform: translate(140%, -140%) rotate(720deg) scale(0);
        opacity: 0;
        color: transparent;
        text-shadow: none;
    }
}

.court-confirm-btn.end-game-ball.hide-anim {
    animation: hitWinner 1.5s forwards ease-in;

}

@keyframes serveBall {
    0% {
        transform: translate(140%, -140%) rotate(-720deg) scale(0);
        opacity: 0;
        color: transparent;
        text-shadow: none;
    }
    70% {
        transform: translate(0, 0) rotate(0) scale(1);
        opacity: 1;
        color: transparent;
        text-shadow: none;
    }
    100% {
        transform: translate(0, 0) rotate(0) scale(1);
        opacity: 1;
        color: white;
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 1), 0 0 10px rgba(0, 0, 0, 0.8);
    }
}


.court-card.selectable .court-confirm-btn.select-court {
    opacity: 0;
}

.court-confirm-btn.select-court.serve-in {
    animation: serveBall 1.5s forwards ease-out;
    will-change: transform;
}

.court-confirm-btn.select-court.serve-out {
    animation: hitWinner 1.5s forwards ease-in;

}

@keyframes endGameAppear {
    0% {
        transform: scale(0);
        opacity: 0;
        color: transparent;
        text-shadow: none;
    }
    10% {
        transform: scale(0.92);
    }
    25% {
        transform: scale(0.88);
    }
    40% {
        transform: scale(0.95);
    }
    55% {
        transform: scale(0.85);
    }
    70% {
        transform: scale(1);
    }
    85% {
        transform: scale(0.8);
    }
    95% {
        transform: scale(1.2);
        opacity: 1;
        color: transparent;
        text-shadow: none;
    }
    100% {
        transform: scale(1);
        opacity: 1;
        color: white;
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 1), 0 0 10px rgba(0, 0, 0, 0.8);
    }
}

.court-card.status-in_progress .end-game-ball {
    opacity: 0;
    transform: scale(0);
}

.court-card.status-in_progress .end-game-ball.animate-in {
    animation: endGameAppear 2s forwards;

}

/* ADD THIS NEW RULE */
