.court-inner {
    position: relative;
    background-color: var(--court-background);
    box-sizing: border-box;
    width: 100%;
    aspect-ratio: 1;
    flex-grow: 1;
    border-left: 2px solid var(--court-line-color);
    border-right: 2px solid var(--court-line-color);
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    margin-bottom: 0.75rem;
}

.court-inner::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 2px;
    background: var(--court-line-color);
    transform: translateY(-50%);
    z-index: 2;
}

.court-inner::after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 12.5%;
    right: 12.5%;
    border-left: 2px solid var(--court-line-color);
    border-right: 2px solid var(--court-line-color);
    background: linear-gradient(to bottom,
        transparent 31%,
        var(--court-line-color) 31%,
        var(--court-line-color) calc(31% + 2px),
        transparent calc(31% + 2px),
        transparent 69%,
        var(--court-line-color) 69%,
        var(--court-line-color) calc(69% + 2px),
        transparent calc(69% + 2px)
    );
    z-index: 1;
}

.center-service-line {
    position: absolute;
    top: 31%;
    bottom: 31%;
    left: 50%;
    width: 2px;
    background-color: var(--court-line-color);
    transform: translateX(-50%);
    z-index: 2;
}

.player-spot {
    position: relative;
    z-index: 3;
}

.player-spot.single-player {
    grid-column: 1 / 3;
}

.player-spot > span {
    position: absolute;
    transform: translate(-50%, -50%);
    width: 50%;
    color: white;
    font-weight: bold;
    font-size: calc(1.1em * var(--font-size-multiplier));
    text-align: center;
    line-height: 1.2;
    text-transform: uppercase;
}

.player-spot.single-player > span {
    left: 50%;
}

.player-spot.top-row > span {
    top: 31%;
}

.player-spot.bottom-row > span {
    top: 69%;
}

/* New Horizontal Positioning */
.player-spot[data-player-pos$="-left"] > span {
    left: 60%;
}

.player-spot[data-player-pos$="-right"] > span {
    left: 40%;
}


