    ================================= */
.mode-selection-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    background-color: rgba(10, 10, 87, 0.85);
    z-index: 7;
    padding: 1rem;
    box-sizing: border-box;
    /* Initially hidden by default */
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s, opacity 0.3s linear;
}

/* Show the overlay when the data attribute is present on the card */
