/* =================================
    PONG ANIMATION STYLES (Singles Base & Doubles Overrides)
    ================================= */

.pong-paused .pong-paddle,
.pong-paused .pong-ball {
    animation-play-state: paused !important; /* Force pause */
}

/* Container for all Pong elements */
.pong-animation-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 3; 
    pointer-events: none;
    overflow: hidden;
}

/* Base style for the paddles (SHARED PROPERTIES) */
.pong-paddle {
    position: absolute;
    height: 6px; 
    background-color: #eafb19;
    border-radius: 3px;
    opacity: 0.9;
    /* Shared Animation Properties */
    animation-duration: 4s; 
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-direction: alternate;
}

/* SINGLES-SPECIFIC STYLES (Horizontal Movement) */
.pong-paddle:not(.double) {
    width: 12.5%; /* Singles Width */
    left: 35%; /* SINGLES START POSITION */
}

/* Doubles Width */
.pong-paddle.double {
    width: 12.5%; /* Doubles/Rookie width (6.25%) */
    transform: translateX(3.125%); /* Static horizontal center adjustment */
}

/* Top Paddle Positioning and Animation */
.pong-paddle.top-paddle {
    top: 17.5%; /* RETAINED VERTICAL POSITION */
}

/* Bottom Paddle Positioning and Animation */
.pong-paddle.bottom-paddle {
    bottom: 17.5%; /* RETAINED VERTICAL POSITION */
}

/* Apply horizontal animation name only to SINGLE paddles */
.pong-paddle:not(.double).top-paddle {
    animation-name: pong-top-move; 
}
.pong-paddle:not(.double).bottom-paddle {
    animation-name: pong-bottom-move; 
}

/* Doubles Horizontal Positioning (from user's code) */
.pong-paddle.double.dl { left: 23.0%; } 
.pong-paddle.double.dr { left: 61.5%; } 

/* Ball Style and Animation (SINGLES BASE) */
.pong-ball {
    position: absolute;
    width: 20px;
    height: 20px;
    background-image: url('source/tennis-ball.png');
    background-size: cover;
    background-color: transparent; /* Changed from white to transparent */
    border-radius: 50%;
    opacity: 0.9;
    transform: translate(-50%, -50%); 
    animation-duration: 4s; 
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-name: pong-ball-move;
}


/* =================================
    PONG KEYFRAMES (Final Merged)
    ================================= */
/* NEW: Spin animation for the in-progress red ball */
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

/* PADDLE MOVEMENT - SINGLES (Lateral movement from user's working code) */
@keyframes pong-top-move {
    0% { transform: translateX(-90.0%); }
    25% { transform: translateX(220.0%); }
    50% { transform: translateX(220.0%); }
    75% { transform: translateX(-90.0%); }
    100% { transform: translateX(-90.0%); }
}
@keyframes pong-bottom-move {
    0% { transform: translateX(220.0%); } 
    25% { transform: translateX(220.0%); }
    50% { transform: translateX(-90.0%); }
    75% { transform: translateX(-90.0%); }
    100% { transform: translateX(220.0%); }
}

/* PADDLE MOVEMENT - DOUBLES TOP RIGHT (TR) - Moves UP, Hits at 25% */
@keyframes pong-move-tr {
    /* 0% to 20%: REST/Retreat (-25px) */
    0%, 20% { transform: translateY(-25px) translateX(3.125%); } 
    /* 25% Hit: Approach (0px) */
    25% { transform: translateY(0) translateX(3.125%); } 
    /* 30% to 100%: REST/Retreat (-25px) */
    55%, 100% { transform: translateY(-25px) translateX(3.125%); }
}

/* PADDLE MOVEMENT - DOUBLES TOP LEFT (TL) - Moves UP, Hits at 75% */
@keyframes pong-move-tl {
    /* 0% to 70%: REST/Retreat (-25px) */
    0%, 70% { transform: translateY(-25px) translateX(3.125%); } 
    /* 75% Hit: Approach (0px) */
    75% { transform: translateY(0) translateX(3.125%); } 
    /* 80% to 100%: REST/Retreat (-25px) */
    20%, 100% { transform: translateY(-25px) translateX(3.125%); }
}

/* PADDLE MOVEMENT - DOUBLES BOTTOM RIGHT (BR) - Moves DOWN, Hits at 0%, 100% */
@keyframes pong-move-br {
    /* 0% Hit: Approach (+25px) */
    0% { transform: translateY(0) translateX(3.125%); } 
    /* 5% to 95%: REST/Retreat (0px) */
    30%, 95% { transform: translateY(25px) translateX(3.125%); }
    /* 100% Hit: Approach (+25px) */
    100% { transform: translateY(0) translateX(3.125%); } 
}

/* PADDLE MOVEMENT - DOUBLES BOTTOM LEFT (BL) - Moves DOWN, Hits at 50% */
@keyframes pong-move-bl {
    /* 0% to 45%: REST/Retreat (0px) */
    0%, 45% { transform: translateY(25px) translateX(3.125%); } 
    /* 50% Hit: Approach (+25px) */
    50% { transform: translateY(0) translateX(3.125%); } 
    /* 55% to 100%: REST/Retreat (0px) */
    80%, 100% { transform: translateY(25px) translateX(3.125%); } 
}


/* BALL MOVEMENT (from user's working code) */
@keyframes pong-ball-move {
    /* Frame 1: Start (Bottom Right) */
    0% { top: 79.5%; left: 68.75%; transform: translate(-50%, -50%) rotate(0deg); }
    
    /* Frame 2: Top Right */
    25% { top: 20.5%; left: 68.75%; transform: translate(-50%, -50%) rotate(900deg); } /* 2.5 turns */
    
    /* Frame 3: Bottom Left */
    50% { top: 79.5%; left: 31.25%; transform: translate(-50%, -50%) rotate(-900deg); } /* 5 turns */
    
    /* Frame 4: Top Left */
    75% { top: 20.5%; left: 31.25%; transform: translate(-50%, -50%) rotate(900deg); } /* 7.5 turns */

    /* Return to Frame 1: Bottom Right */
    100% { top: 79.5%; left: 68.75%; transform: translate(-50%, -50%) rotate(-900deg); } /* 10 turns */
}

