.scrolling-ball {
    display: inline-block;      /* Keeps it in the text flow */
    font-size: 2.0rem;          /* Overall size control */
    vertical-align: middle;     /* Align container with text */
    margin: 0 0.5rem 0 0.75rem; /* Spacing */
