/* --- Styles for the INNER span (Emoji Centering & Animation) --- */
.scrolling-ball > span {
    display: flex;              /* Use Flexbox for centering */
    align-items: center;        /* Vertically center content (the emoji) */
    justify-content: center;    /* Horizontally center content (the emoji) */
    width: 100%;                /* Ensure inner span fills outer */
    height: 100%;               /* Ensure inner span fills outer */
    animation-name: headerBallSpinHop;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-duration: 1s;
    transform-origin: center center; /* Keep explicit origin */
    
}

/* --- NEW: Styles for the scrolling flame icon --- */
