    display: inline-block;
    font-size: 2.5rem;      /* Match ball size */
    vertical-align: middle;
    transform: rotate(90deg); /* Rotate 90 degrees */
    margin-right: 0.75rem; /* Space between flame and text */
    margin-left: -1.5rem;
    padding-right: 0.6rem;
    /* Optional: Add a subtle pulse or flicker animation if desired */
    animation: flicker 0.8s infinite alternate;

}

@keyframes flicker {
  0%, 100% { opacity: 1; transform: scale(1) rotate(90deg); }
  25% { opacity: 0.6; transform: scale(0.98) rotate(90deg); }
  50% { opacity: 0.8; transform: scale(1.02) rotate(90deg); }
  75% { opacity: 0.5; transform: scale(0.96) rotate(90deg); }
}

/* --- NEW: Animation specifically for the header announcement ball --- */
