    opacity: 0.7;
}
.checklist-container.read-only .checklist-item input[type="checkbox"]:checked + span {
    /* Keep line-through for clarity */
    text-decoration: line-through;
    color: var(--neutral-color);
    font-style: italic;
}
 .checklist-container.read-only .checklist-item input[type="checkbox"]:not(:checked) + span {
    /* Ensure unchecked items look normal but aren't interactive */
     text-decoration: none;
     color: var(--dark-text);
     font-style: normal;
}


/* Checklist Section Headers */
.checklist-section-header {
    background-color: #f8f9fa;
    padding: 0.6rem 1rem;
    margin: 0; /* Remove margin */
    font-weight: bold;
    color: var(--primary-blue);
    border-bottom: 1px solid var(--border-color);
    border-top: 1px solid var(--border-color);
    font-size: calc(1.1rem * var(--font-size-multiplier));
}
.checklist-container > .checklist-section-header:first-of-type {
    border-top: none; /* Remove top border for the very first header */
}

#checklist-history-modal .modal-actions {
    justify-content: center; /* Center buttons */
    gap: 1rem;
}
 #checklist-history-modal .modal-actions button.hidden {
    display: none;
 }

 /* Ensure Checklist History Button inherits admin icon styles */
#checklist-modal .admin-icon-btn {
    background: none; /* Explicitly set background to none */
    border: none;
    /* Apply size multiplier to font size */
    font-size: calc(1.5rem * var(--display-size-multiplier));
    cursor: pointer;
    color: var(--neutral-color);
    transition: color 0.2s;
    padding: 0;
    line-height: 1;
    display: inline-block;
    /* Apply size multiplier to width and height */
    width: calc(28px * var(--display-size-multiplier));
    height: calc(28px * var(--display-size-multiplier));
