}

#checklist-modal .admin-icon-btn:hover {
    color: var(--primary-blue);
}

/* Ensure the icon itself scales */
#checklist-modal .admin-icon-btn i {
     font-size: calc(1.5rem * var(--display-size-multiplier));
     line-height: 1;
}

/* =================================
    Event Management Modals (NEW)
