/* Event Create/Edit Modal */
#event-create-edit-modal .modal-content {
    max-height: 85vh; /* Allow more height for scrolling */
    display: flex; /* Needed for scrollable area */
    flex-direction: column;
}

/* Style the logo similar to checkout modal */
#event-create-edit-modal .checkout-thanks-logo {
    display: block;
    margin: -1rem auto 1rem auto; /* Adjust margins as needed */
    height: 70px;
    width: auto;
    filter: url(#recolor-to-primary-blue);
    pointer-events: none;
}

.event-form-grid {
    display: grid;
    grid-template-columns: 1fr 1fr; /* Two columns */
    gap: 1rem 1.5rem; /* Row gap, Column gap */
    margin-top: 1rem;
    flex-grow: 1; /* Allows this area to scroll */
    overflow-y: auto; /* Enable vertical scroll */
    min-height: 0; /* Important for flex scroll */
    padding: 0.5rem; /* Padding inside scroll area */
    margin-bottom: 1rem; /* Space before modal actions */
}

.form-group {
    display: flex;
    flex-direction: column;
}

.form-group.span-two {
    grid-column: span 2; /* Make element span both columns */
}

.form-group label {
    margin-bottom: 0.3rem;
    font-weight: 500;
    font-size: calc(0.9rem * var(--font-size-multiplier));
    color: var(--primary-blue);
}

/* Style inputs, select */
#event-create-edit-modal input[type="text"],
#event-create-edit-modal input[type="date"],
#event-create-edit-modal input[type="time"],
#event-create-edit-modal select {
    padding: 0.6rem 0.8rem;
    font-size: calc(1rem * var(--font-size-multiplier));
    border: 1px solid var(--border-color);
    border-radius: 5px;
    box-sizing: border-box; /* Include padding in width */
    width: 100%; /* Make inputs fill column */
}
/* Readonly inputs (for keypad) */
/* --- MODIFIED: Style for multiline body text textareas --- */
#event-create-edit-modal .event-body-input {
    /* Set a fixed height that fits ~3 lines and prevent resizing */
    height: 100px; /* Adjusted height for clean 3-4 line display */
    min-height: 100px; /* Ensure minimum height */
    resize: none;
    /* Reset padding from the previous attempt, use standard padding */
    padding: 0.6rem 0.8rem;
    font-size: calc(1rem * var(--font-size-multiplier));
    line-height: 1.5; /* Ensure spacing between lines */
    box-sizing: border-box;
    /* Ensure text aligns to the top */
    vertical-align: top; 
}

/* Readonly inputs (for keypad) */
#event-create-edit-modal input[readonly],
#event-create-edit-modal textarea[readonly] { /* ADDED: textarea[readonly] */
    background-color: #e9ecef; 
    cursor: pointer;
}

/* Slider value display */
.slider-value {
    font-weight: bold;
    color: var(--dark-text);
}

/* Range slider styling */
#event-create-edit-modal input[type="range"] {
    width: 100%;
    margin-top: 0.5rem;
    cursor: pointer;
}

/* Checkbox group styling */
.checkbox-group {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    margin-top: 0.5rem; /* Align with other fields */
}
.checkbox-group label {
    margin-bottom: 0; /* Reset label margin */
    color: var(--dark-text); /* Standard text color */
    font-weight: normal;
}
.checkbox-group input[type="checkbox"] {
    width: 1.1em;
    height: 1.1em;
}

/* Radio button group styling */
.radio-group-container {
    display: flex;
    flex-direction: column; /* Stack label and buttons */
}
.radio-group-container > label { /* Target only the main label */
     margin-bottom: 0.3rem;
     font-weight: 500;
     font-size: calc(0.9rem * var(--font-size-multiplier));
     color: var(--primary-blue);
}
.radio-group-container .radio-group {
    display: flex;
    gap: 1rem;
    padding: 0.5rem;
    background-color: #f0f0f0;
    border-radius: 8px;
    justify-content: space-around; /* Distribute options */
}
.radio-group-container .radio-group label {
    margin-bottom: 0;
    font-weight: normal;
    color: var(--dark-text);
    display: flex;
    align-items: center;
    gap: 0.3rem;
}
.separate-checkbox {
     margin-top: 0.8rem;
     padding-top: 0.8rem;
     border-top: 1px dashed var(--border-color);
}


/* Interested Players List (Admin View) */
#interested-players-section h4 {
    margin-bottom: 0.5rem;
    color: var(--primary-blue);
}
#interested-players-list {
    max-height: 150px; /* Limit height */
    border: 1px solid var(--border-color);
    border-radius: 5px;
    padding: 0;
    margin: 0;
}
#interested-players-list li {
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 1rem;
    font-size: calc(0.95rem * var(--font-size-multiplier));
    border-bottom: 1px solid #f0f0f0;
}
#interested-players-list li:last-child {
    border-bottom: none;
}


/* =================================
    Screensaver Styles (NEW)
   ================================= */

#screensaver-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: var(--primary-blue);
    z-index: 9999;
    opacity: 0;
    visibility: hidden;
    transition: opacity 1s ease-in-out, visibility 0s linear 1s;
    cursor: pointer;
    overflow: hidden;
}

#screensaver-overlay:not(.hidden) {
    opacity: 1;
    visibility: visible;
    transition: opacity 1s ease-in-out, visibility 0s linear 0s;
}

#screensaver-content {
    width: 100%;
    height: 100%;
    display: flex; /* Keep as flex */
    /* REMOVE flex-direction: column; */
    color: white;
    overflow: hidden;
    opacity: 0;
    transition: opacity 1s ease-in-out;
}

#screensaver-content.visible {
    opacity: 1;
}

/* Main event layout - split into left images and right content */
.screensaver-event-layout {
    display: grid;
    grid-template-columns: 33vw 1fr; /* Keep column widths */
    grid-template-rows: 1fr; /* Single row */
    gap: 0;
    flex: 1;
    overflow: hidden;
    padding: 0;
    /* --- NEW: Ensure layout takes full height --- */
    height: 100%;
    width: 100%;
    /* --- END NEW --- */
}

/* Left column - Event images stacked vertically */
.screensaver-left-column {
    /* REMOVED max-width: 500px; */
    background: var(--primary-blue);
    display: flex;
    flex-direction: column;
    gap: 0;
    padding: 0;
    overflow: hidden;
    /* REMOVED border-right: 4px solid #a3e635; */
    /* Optional: Remove box-shadow if you don't want the subtle shadow either */
    /* box-shadow: 2px 0 4px rgba(163, 230, 53, 0.3); */
    height: 100%;
}

.screensaver-event-image {
    width: 100%;
    height: 50%; /* Each takes half */
    object-fit: fill;
    display: block;
    /* --- NEW: Prevent shrinking --- */
    flex-shrink: 0;
    /* --- END NEW --- */
}

.screensaver-event-image:only-child {
    height: 100%; /* Single image takes full height */
}

.screensaver-placeholder-image {
    width: 100%;
    height: 100%;
    /* background: linear-gradient(135deg, #1a1a4a 0%, #0a0a2e 100%); /* Old Gradient */
    background-color: var(--primary-blue); /* NEW Solid Color */
}

/* Right column - Event details on blue background */
.screensaver-right-column {
    background: var(--primary-blue);
    /* Remove padding: 3rem 4rem; */
    display: flex;
    flex-direction: column; /* Stack main content and banners */
    overflow: hidden; /* Prevent parent scroll */
    color: white;
    /* --- NEW: Ensure full height --- */
    height: 100%;
    /* --- END NEW --- */
}

/* --- NEW RULE: Wrapper for main text content in right column --- */
.screensaver-right-column-main-content {
    flex-grow: 1; /* Allow this section to take up available space */
    /* REMOVE overflow-y: auto; */
    /* ADD display: flex and flex-direction */
    display: flex;
    flex-direction: column;
    /* END ADD */
    padding: 1rem 4rem; /* Apply original padding here */
    min-height: 0; /* Important for flex */
    overflow: hidden; /* Prevent accidental overflow if flex doesn't fully contain */
}

.screensaver-club-logo {
    width: 320px;
    height: auto;
    margin-top: -2rem;
    display: block;
    filter: brightness(0) invert(1);
    /* --- NEW: Prevent shrinking --- */
    flex-shrink: 0;
    /* --- END NEW --- */
}

.screensaver-event-heading {
    font-size: 3rem; /* Reduced from 3.5rem (approx 57%) */
    font-weight: bold;
    text-align: center;
    margin: 0 0 1rem 0; /* Reduced bottom margin */
    line-height: 1.1;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    /* --- NEW: Prevent shrinking --- */
    flex-shrink: 0;
    /* --- END NEW --- */
}

/* Date and time row with trophy icon */
.screensaver-datetime-row {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2rem;
    margin: 0 0 1.5rem 0;
    padding: 0.25rem 0;
    border-top: 3px solid white;
    border-bottom: 3px solid white;
    position: relative; /* Keep this */
}

.screensaver-date,
.screensaver-time {
    flex: 1;
    text-align: center;
}

.screensaver-date .date-value,
.screensaver-time .time-value {
    font-size: 1.8rem; /* Reduced from 2.2rem */
    font-weight: bold;
    color: white;
    text-transform: uppercase;
}

.screensaver-trophy {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: var(--primary-blue);
    padding: 0 1rem;
    z-index: 1;
    height: 6rem; /* Increased from 4.8rem - adjust as needed */
    display: flex;
    align-items: center;
    justify-content: center;
}
/* --- NEW: Style for the tennis ball image in the date/time row --- */
.screensaver-datetime-row .screensaver-trophy img {
    height: 5.8rem; /* Keep or adjust as needed */
    width: auto;
    display: block;
    margin: 0 auto;
    filter: drop-shadow(0 2px 3px rgba(0, 0, 0, 0.5));
}

/* Body text */
.screensaver-body-text {
    /* --- MODIFY THIS LINE --- */
    font-size: 1.6rem; /* Increased from 1.4rem */
    /* --- END MODIFY --- */
    line-height: 1.6;
    margin: 0 0 1rem 0; /* Reduced bottom margin */
    text-align: center;
    /* --- Allow shrinking, but don't hide overflow --- */
    flex-shrink: 1;
    /* REMOVE overflow: hidden; */
    /* --- END CHANGE --- */
}

.screensaver-body-text p {
    margin: 0 0 1rem 0;
    /* --- ADD THIS LINE --- */
    white-space: pre-line; /* Preserves line breaks, collapses extra spaces */
    /* --- END ADD --- */
}
.screensaver-body-text p:last-child {
    margin-bottom: 0;
}

/* Cost display */
.screensaver-cost {
    font-size: 1.6rem;
    font-weight: 600;
    margin: 1.5rem 0 0.5rem 0;
    text-align: center;
}

.screensaver-discount {
    font-size: 1.3rem;
    margin: 0.5rem 0 1.5rem 0;
    text-align: center;
    font-style: italic;
}

/* RSVP section */
.screensaver-rsvp {
    font-size: 1.8rem;
    font-weight: bold;
    text-align: center;
    margin: 2rem 0 0.5rem 0;
    color: #fef08a;
    background: transparent !important;
}

.screensaver-volunteers {
    font-size: 1.5rem;
    font-weight: bold;
    text-align: center;
    margin: 0.5rem 0 1.5rem 0;
    color: #fef08a;
}

.screensaver-rsvp-text {
    font-size: 1.3rem;
    text-align: center;
    margin: 1rem 0;
}

.screensaver-contact-name {
    font-size: 1.3rem;
    text-align: center;
    margin: 1rem 0 0 0;
}

/* Style the "I am interested" button */
#screensaver-interested-btn {
    /* Existing styles */
    padding: 0.8rem 1.5rem;
    font-size: calc(1.2rem * var(--font-size-multiplier));
    font-weight: bold;
    border-radius: 8px;
    cursor: pointer;
    background-color: var(--confirm-color);
    color: white;
    border: none;
    transition: background-color 0.2s;
    width: fit-content;
    /* margin: 2rem auto 0 auto; /* Remove original margin */
    z-index: 11000; /* Keep z-index */

    /* Added styles for centering */
    display: block; /* Make it a block element to center with margin */
    margin-left: auto;
    margin-right: auto;
    margin-top: 1rem; /* Adjust top margin if needed */
    flex-shrink: 0;
}

#screensaver-interested-btn:hover {
    background-color: var(--confirm-hover-color);
}

/* Contact bar at bottom - yellow/lime background */
.screensaver-contact-bar {
    background: linear-gradient(135deg, #bef264 0%, #a3e635 100%);
    padding: 1.5rem 2rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 2rem;
    flex-wrap: wrap;
    /* --- NEW: Prevent shrinking --- */
    flex-shrink: 0;
    /* --- END NEW --- */
}

.screensaver-contact-item {
    display: flex;
    align-items: center;
    gap: 0.8rem;
    color: #1a1a1a;
}

.contact-icon {
    font-size: 2rem;
    line-height: 1;
}

.contact-text {
    font-size: 1.4rem;
    font-weight: 600;
}

/* Open to banner at very bottom */
.screensaver-open-banner {
    /* background: #0a0a2e; /* Old Darker Blue */
    background-color: var(--primary-blue); /* NEW Solid Color */
    color: #fef08a;
    text-align: center;
    padding: 1.2rem;
    font-size: 1.6rem;
    font-weight: bold;
    letter-spacing: 1px;
    flex-shrink: 0;
}

/* Styles for showing the main app view during screensaver */
#screensaver-overlay.show-main-app {
    background: none !important;
    background-color: transparent !important;
    background-image: none !important;
    pointer-events: none;
}

#screensaver-overlay.show-main-app #screensaver-content {
    display: none !important;
}

/* Add flex-shrink: 0 to prevent these from shrinking */
.screensaver-right-column-main-content .screensaver-club-logo,
.screensaver-right-column-main-content .screensaver-datetime-row,
.screensaver-right-column-main-content .screensaver-cost,
.screensaver-right-column-main-content .screensaver-discount,
#screensaver-interested-btn {
    flex-shrink: 0;
}

/* Adjust margins for tighter vertical fit */
.screensaver-right-column-main-content .screensaver-club-logo {
    margin: 0 auto 1rem auto; /* Reduced bottom margin */
}

.screensaver-right-column-main-content .screensaver-datetime-row {
    margin: 0 0 1.5rem 0; /* Reduced bottom margin */
    padding: 1rem 0;      /* Reduced vertical padding */
}

.screensaver-right-column-main-content .screensaver-cost {
    margin: 1rem 0 0.25rem 0; /* Adjusted margins */
    flex-shrink: 0;
    /* --- NEW: Push down --- */
    margin-top: auto;
    /* --- END NEW --- */
}

.screensaver-right-column-main-content .screensaver-discount {
    margin: 0.25rem 0 1rem 0; /* Adjusted margins */
}


/* Responsive adjustments */
@media (max-width: 1400px) {
    .screensaver-event-layout {
        grid-template-columns: 30vw 1fr;
    }
    
    .screensaver-right-column {
        padding: 2rem 3rem;
    }
    
    .screensaver-event-heading {
        font-size: 3rem;
    }
    
    .screensaver-club-logo {
        width: 260px;
    }
}

@media (max-width: 1024px) {
    .screensaver-event-layout {
        grid-template-columns: 1fr;
        grid-template-rows: auto 1fr;
    }
    
    .screensaver-left-column {
    max-width: 500px;
        flex-direction: row;
        height: 250px;
    }
    
    .screensaver-event-image {
        width: 50%;
        height: 100%;
    }
    
    .screensaver-right-column {
        padding: 2rem;
    }
    
    .screensaver-event-heading {
        font-size: 2.5rem;
    }
    
    .screensaver-contact-bar {
        flex-direction: column;
        gap: 1rem;
        padding: 1rem;
    }
}



/* --- NEW: Icon Input Group Styling --- */
.icon-input-group {
    position: relative; /* Needed for absolute positioning of the icon */
}

.icon-input-group i.mdi {
    position: absolute;
    left: 10px; /* Adjust as needed */
    top: 50%;
    transform: translateY(-50%);
    font-size: calc(1.5rem * var(--font-size-multiplier)); /* Adjust icon size */
    color: var(--neutral-color); /* Icon color */
    pointer-events: none; /* Make icon non-interactive */
}

/* Adjust input padding to make space for the icon */
.icon-input-group input[type="text"] {
    padding-left: 40px !important; /* Increase left padding */
}


/* --- Responsive adjustments for Event form --- */
@media (max-width: 600px) {
    .event-form-grid {
        grid-template-columns: 1fr; /* Stack to single column */
    }
    .form-group.span-two {
        grid-column: span 1; /* Reset span */
    }
    #event-create-edit-modal .modal-content {
        padding: 1rem; /* Reduce padding on mobile */
    }
    .screensaver-details {
        grid-template-columns: 1fr; /* Stack details */
         font-size: calc(1rem * var(--font-size-multiplier));
    }
     .screensaver-details p {
        flex-direction: column; /* Stack label/value on mobile */
        align-items: center;
        text-align: center;
        margin-bottom: 0.5rem;
    }
    .screensaver-details strong {
        margin-right: 0;
        margin-bottom: 0.2rem;
    }
    .screensaver-details span {
        text-align: center;
    }
    /* Reset spans for stacked items */
    .screensaver-body1,
    .screensaver-body2,
    .screensaver-contact,
    .screensaver-open-to {
        grid-column: span 1;
    }
    .screensaver-body1 span,
    .screensaver-body2 span,
    .screensaver-contact span,
    .screensaver-open-to span {
        text-align: center;
    }
}
