/* ================================
   Drag & Drop Styles
   ================================ */
.draggable-player {
    cursor: grab;
    user-select: none;
    -webkit-user-select: none;
}

.draggable-player:active {
    cursor: grabbing;
}

.player-spot[draggable="true"] {
    transition: transform 0.2s, box-shadow 0.2s;
}

.player-spot.dragging {
    opacity: 0.5;
    transform: scale(0.95);
}

.player-spot.drag-over {
    box-shadow: 0 0 0 3px var(--primary-green);
    background-color: rgba(40, 167, 69, 0.1);
    transform: scale(1.05);
}

.player-spot.drag-over-invalid {
    box-shadow: 0 0 0 3px var(--cancel-color);
    background-color: rgba(220, 53, 69, 0.1);
    cursor: not-allowed;
}

/* Available players list - drag targets */
#availablePlayersList li.drag-over {
    background-color: var(--light-blue);
    box-shadow: 0 0 0 2px var(--primary-green);
    transform: scale(1.02);
}

#availablePlayersList li.drag-over-invalid {
    background-color: #fbe9eb;
    box-shadow: 0 0 0 2px var(--cancel-color);
}

/* Double-click to enable dragging */
.player-spot[draggable="true"] {
    position: relative;
}

.player-spot.drag-enabled {
    animation: drag-pulse 1.5s ease-in-out infinite;
    box-shadow: 0 0 0 2px var(--primary-green);
    cursor: grab !important;
}

.player-spot.drag-enabled::after {
    content: "Drag me!";
    position: absolute;
    top: -20px;
    left: 50%;
    transform: translateX(-50%);
    background-color: var(--primary-green);
    color: white;
    padding: 2px 8px;
    border-radius: 4px;
    font-size: 0.7rem;
    white-space: nowrap;
    pointer-events: none;
    animation: fade-in 0.3s ease-out;
}

@keyframes drag-pulse {
    0%, 100% {
        box-shadow: 0 0 0 2px var(--primary-green);
    }
    50% {
        box-shadow: 0 0 0 4px var(--primary-green), 0 0 8px rgba(40, 167, 69, 0.4);
    }
}

@keyframes fade-in {
    from {
        opacity: 0;
        transform: translateX(-50%) translateY(-5px);
    }
    to {
        opacity: 1;
        transform: translateX(-50%) translateY(0);
    }
}

/* Disable cursor for non-enabled draggable players */
.player-spot[draggable="true"]:not(.drag-enabled) {
    cursor: default;
}

