/* ============================================================================
   GATE CONTROLS STYLING - Add this to your style.css
   ============================================================================ */

/* Gate Controls Container */
.gate-controls {
    display: flex;
    gap: 10px;
    align-items: center;
    margin-left: 15px;
}

/* Gate Button Styling */
.gate-btn {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    padding: 0px 0px;
    background: transparent;
    border: none;
    border-radius: 10px;
    color: white;
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 0.85em;
    min-width: 85px;
    box-shadow: none;
}

.gate-btn:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(102, 126, 234, 0.5);
    background: transparent;
}

.gate-btn:active {
    transform: translateY(0);
    box-shadow: none;
}

/* Pulsing Animation when gate is activated */
.gate-btn.pulsing {
    animation: pulse-gate 1s ease-in-out;
    pointer-events: none; /* Prevent double-clicks during pulse */
}

@keyframes pulse-gate {
    0% {
        opacity: 1;
        transform: scale(1);
    }
    25% {
        opacity: 0.7;
        transform: scale(0.95);
    }
    50% {
        opacity: 1;
        transform: scale(1.05);
    }
    75% {
        opacity: 0.7;
        transform: scale(0.95);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
}

/* Gate Button Icon */
.gate-btn i {
    font-size: 1.8em;
    margin-bottom: 4px;
}

/* Gate Button Text */
.gate-btn span {
    font-size: 0.75em;
    font-weight: 600;
    letter-spacing: 0.3px;
}

/* Disabled State */
.gate-btn:disabled {
    opacity: 0.5;
    cursor: not-allowed;
    pointer-events: none;
}

/* Responsive Design */
@media (max-width: 1024px) {
    .gate-controls {
        gap: 8px;
